export default {
	data() {
		return {
			showSchool: false,
			columnsSchool: [],
			showGrade: false,
			columnsGrade: ['XX年级', 'XX年级', 'XX年级', 'XX年级', 'XX年级', 'XX年级'],
			showClass: false,
			columnsClass: [],
			selectSchool: '',
			selectClass: '',
			selectGrade: '',
			schoolId: ''
		}
	},
	methods: {
		// 返回
		goBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 弹框
		showBox(val) {
			if (val == 'School') {
				this.getSchoolList()
				this.showSchool = !this.showSchool
			}
			if (val == 'Grade') {
				this.showGrade = !this.showGrade
			}
			if (val == 'Class') {
				this.getClassList()
				this.showClass = !this.showClass
			} else {}
		},
		onCancel(val) {
			if (val == 'School') {
				this.showSchool = false
			}
			if (val == 'Grade') {
				this.showGrade = false
			}
			if (val == 'Class') {
				this.showClass = false
			} else {}
		},
		onConfirmSchool(e) {
			this.showSchool = false
			console.log(e)
			this.selectSchool = e.detail.value.schoolName
		},
		onConfirmGrade(e) {
			this.showGrade = false
			this.selectGrade = e.detail.value
		},
		onConfirmClass(e) {
			console.log(e)
			this.showClass = false
			this.selectClass = e.detail.value.className
		},
		// 获取学校下拉列表
		getSchoolList() {
			this.$minApi.schoolList({}).then(data => {
				// console.log(data)
				data.data.forEach(val => {
					val.text = val.schoolName
				})
				this.columnsSchool = data.data
				console.log(this.columnsSchool)
			})
		},
		// 获取班级下拉列表
		getClassList() {
			this.$minApi.classList({
				schoolId: this.schoolId
			}).then(data => {
				console.log(data)
				data.data.forEach(val => {
					console.log(val)
					val.text = val.className
				})
				this.columnsClass = data.data
				console.log(this.columnsClass)
			})
		},
	},
	onLoad() {
		this.schoolId = getApp().globalData.schoolId
	},
	created() {}


}
